<template>
    <div>
        <div class="header mb-5">
            <vs-button color="primary" type="filled" icon-pack="feather" icon="icon-arrow-left" style="min-width: 38px"
                       @click="$router.push('/bagholders')"></vs-button>
            <h3>{{ $store.getters['bagholders/getId']($route.params.id).name }}</h3>
        </div>

        <div class="flex flex-wrap -mx-2">
            <div class="w-full lg:w-1/4 xl:w-1/4 mb-2 px-4" v-for="i in $store.getters['bagholders/getId']($route.params.id).modules">
                <vx-card class="mb-4 pointer" @click="edit(i.id, i.name)">
                    <div slot="no-body">
                        <div class="item">
                            <div class="label">
                                {{ i.name }}
                            </div>
                            <div class="sub-label">
                                Нажмите для редактирования
                            </div>
                        </div>
                    </div>
                </vx-card>
            </div>
        </div>

        <vs-prompt
            title="Переименовать ячейку"
            accept-text="Переименовать"
            cancel-text="Отмена"
            @cancel="cancelEdit"
            @accept="acceptEdit"
            @close="cancelEdit"
            :active.sync="activeEdit">
            <div class="con-exemple-prompt">
                <div class="mb-1">Введите название</div>
                <vs-input autofocus class="w-full" v-model="name" @keyup.enter="acceptEdit"/>
            </div>
        </vs-prompt>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                activeEdit: false,
                name: '',
                id: ''
            }
        },
        methods: {
            edit(id, name){
                this.id = id;
                this.name = name;
                this.activeEdit = true;
            },
            async acceptEdit(){
                try{
                    await this.$http.post('/modules/' + this.id, {name: this.name})
                }catch (e) {
                    console.log(e)
                }
                this.cancelEdit()
            },
            cancelEdit(){
                this.id = '';
                this.name = '';
                this.activeEdit = false;
            }
        }
    }
</script>

